import { Component, Input, OnInit, ViewChild } from "@angular/core";
import {
  ChangeEventArgs,
  DropDownListComponent,
} from "@syncfusion/ej2-angular-dropdowns";
import { TreeGridComponent } from "@syncfusion/ej2-angular-treegrid";
import { sampleData } from "../datasource";
import { PageSettingsModel } from "@syncfusion/ej2-angular-grids";

@Component({
  selector: "app-parentfiltering",
  templateUrl: "./parentfiltering.component.html",
  styleUrls: ["./parentfiltering.component.css"],
})
export class ParentfilteringComponent implements OnInit {
  public data: Object[];
  public pageSettings: PageSettingsModel;
  public filterSettings: Object;
  public d1data: Object;
  public ddlfields: Object;
  public d2data: Object;
  public typefields: Object;

  @Input() public treegrid: TreeGridComponent;
  @ViewChild("dropdown1", { static: true })
  public dropdown1: DropDownListComponent;
  @ViewChild("dropdown2", { static: true })
  public dropdown2: DropDownListComponent;
  constructor() {}

  ngOnInit() {
    this.data = sampleData;
    this.filterSettings = { type: "Menu", hierarchyMode: "Parent" };
    this.ddlfields = { text: "mode", value: "id" };
    this.typefields = { text: "mode", value: "id" };
    this.d1data = [
      { id: "Parent", mode: "Parent" },
      { id: "Child", mode: "Child" },
      { id: "Both", mode: "Both" },
      { id: "None", mode: "None" },
    ];
    this.d2data = [
      { id: "Menu", mode: "Menu" },
      { id: "Excel", mode: "Excel" },
    ];
  }
  onChange(e: ChangeEventArgs): void {
    let mode: any = <string>e.value;
    this.treegrid.filterSettings.hierarchyMode = mode;
    this.treegrid.clearFiltering();
  }
  change(e: ChangeEventArgs): void {
    let type: any = <string>e.value;
    this.treegrid.filterSettings.type = type;
    this.treegrid.clearFiltering();
  }
}
