import { Component, OnInit, ViewChild, ViewEncapsulation } from "@angular/core";
import { sampleData } from "./datasource";
import {
  EditSettingsModel,
  TreeGridComponent,
} from "@syncfusion/ej2-angular-treegrid";
import { ToolbarItems } from "@syncfusion/ej2-angular-grids";
import { ContextMenuComponent } from "@syncfusion/ej2-angular-navigations";

@Component({
  selector: "app-root",
  templateUrl: "app.component.html",
  encapsulation: ViewEncapsulation.None,
})
export class AppComponent implements OnInit {
  @ViewChild("treegrid", { static: true })
  public treegrid: any;
  @ViewChild('target', { static: true })
  public contextmenu: ContextMenuComponent;
  public data: Object[] = [];
  public pageSettings: Object;
  public selectOptions: Object;
  public toolbar: string[];
  public toolbars: string[];
  public filterSettings: Object;
  public ddlfields: Object;
  public typefields: Object;
  public sortSettings: Object;
  public contextMenuItems: any[] = [];
  public selectionSettings: Object;
  public tasknamerules: Object;
  public taskidrules: Object;
  public startdaterules: Object;
  public enddaterules: Object;
  public durationrules: Object;
  public progressrules: Object;
  public editSettings: Object;
  public toolbarOptions: ToolbarItems[];
  public editing: EditSettingsModel;
  public formatOptions: Object;
  public editOptions: Object;
  static taskID: number;
  taskName: any;
  taskID: any;
  isCoping: boolean;
  public rowIndex: number;
  public cellIndex: number;
  public columnKey: string;
  public column: any;
  public selectionOptions: Object;
  columns = [
    { key: "taskID", headerText: "Task ID" },
    { key: "taskName", headerText: "Task Name" },
    { key: "duration", headerText: "Duration" },
  ];

  constructor() {
    this.isCoping = false;
  }
  ngOnInit(): void {
    this.data = sampleData;
    this.taskID = 1000;
    // this.editSettings = {
    //   allowEditing: true,
    //   allowAdding: true,
    //   allowDeleting: true,
    //   showDeleteConfirmDialog: true,
    //   mode: "Batch",
    // };
    this.toolbarOptions = [
      "Add",
      "Edit",
      "Delete",
      "ColumnChooser",
    ];

    this.editing = {
      allowEditing: true,
      allowAdding: true,
      allowDeleting: true,
      mode: "Dialog",
    },
      (this.formatOptions = { format: "M/d/y hh:mm a", type: "dateTime" });
    this.editOptions = { params: { format: "M/d/y hh:mm a" } };
    this.selectOptions = { type: "Multiple" };
    this.pageSettings = { pageCount: 5, pageSizes: true };
    this.contextMenuItems = [
      "AutoFit",
      "AutoFitAll",
      { text: "Rename Column", target: ".e-columnheader", id: "customHeader" },
      {
        text: "Change Type", target: ".e-columnheader", id: "customHeader",
        items: [
          { text: "Text", id: "customText" },
          { text: "Number", id: "customNumber" },
          { text: "Date", id: "customDate" },
          { text: "DropDownList", id: "customDropDownList" },
          { text: "Boolean", id: "customBoolean" }
        ]
      },
      { text: "Font Size", target: ".e-columnheader", id: "customFontSize" },
      { text: "Font Color", target: ".e-columnheader", id: "customFontColor" },
      {
        text: "Background Color",
        target: ".e-columnheader",
        id: "customBackgroundColor",
      },
      {
        text: "Text Alignment",
        target: ".e-columnheader",
        id: "customTextAlignment",
      },
      "SortAscending",
      "SortDescending",
      "Edit",
      "Delete",
      "Save",
      "Cancel",
      "PdfExport",
      "ExcelExport",
      "CsvExport",
      "FirstPage",
      "PrevPage",
      "LastPage",
      "NextPage",
      { text: "Copy", target: ".e-content", id: "customCopy" },
      { text: "Cut", target: ".e-content", id: "customCut" },
      { text: "Add Row", target: ".e-content", id: "customAddRow" },
      { text: "Add Child Row", target: ".e-content", id: "customAddChildRow" },
      { text: "PasteNext", target: ".e-content", id: "customPasteNext" },
      { text: "PasteChild", target: ".e-content", id: "customPasteChild" },
    ];
    this.selectionOptions = {
      type: "Multiple",
      mode: "Row",
      cellSelectionMode: "Box",
    };
    this.filterSettings = { type: "Menu", hierarchyMode: "Parent" };
    this.ddlfields = { text: "mode", value: "id" };
    this.typefields = { text: "mode", value: "id" };
    this.sortSettings = {
      columns: [
        { field: "taskID", direction: "Ascending" },
        { field: "taskName", direction: "Ascending" },
      ],
    };
    this.selectionSettings = { type: "Multiple" };
    this.tasknamerules = { required: true };
    this.taskidrules = { required: true };
    this.startdaterules = { date: true, required: true };
    this.enddaterules = { date: true, required: true };
    this.durationrules = { number: true, min: 0, required: true };
    this.progressrules = { number: true, min: 0, required: true };
  }

  contextMenuOpen(args): void {
    if (args.column != null) {
      this.column = this.treegrid.getColumnByField(args.column.field);
    }

    this.rowIndex = args.rowInfo.rowIndex;
    this.cellIndex = args.rowInfo.cellIndex;
  }

  contextMenuSwitcher(event): void {
    debugger;
    if (event.target.checked === true) {
      (document.querySelector('.e-contextmenu-wrapper') as any).style.display = 'block';
    } else {
      (document.querySelector('.e-contextmenu-wrapper') as any).style.display = 'none';
    }
  }
  contextMenuClick(args): void {
    if (args.item.id === "customHeader") {
      this.column.headerText = prompt("Enter Column Name", "");
      this.treegrid.refreshColumns();
    } else if (args.item.id === "customFontSize") {
      let element =
        document.querySelectorAll(".e-headercell")[this.column.index];
      (element as any).children[0].style.fontSize = prompt("Enter Font Size", "");
    } else if (args.item.id === "customFontColor") {
      let element =
        document.querySelectorAll(".e-headercell")[this.column.index];
      (element as any).children[0].style.color = prompt("Enter Font Color", "");
    } else if (args.item.id === "customBackgroundColor") {
      let element =
        document.querySelectorAll(".e-headercell")[this.column.index];
      (element as any).style.backgroundColor = prompt(
        "Enter Background Color",
        ""
      );
    } else if (args.item.id === "customTextAlignment") {
      let alignment = prompt("Enter Text Alignment", "");
      document.querySelectorAll(".e-rowcell").forEach((e, i) => {
        if ((document.querySelectorAll(".e-rowcell")[i] as any).ariaColIndex == this.column.index) {
          let element =
            document.querySelectorAll(".e-rowcell")[i];
          (element as any).style.textAlign = alignment;
        }
      });
    } else if (args.item.id === "customCopy") {
      this.isCoping = true;
      this.treegrid.copy();
      this.treegrid.getSelectedRowIndexes().forEach((index, i) => {
        debugger;
        let element =
          document.querySelectorAll(".e-row")[index];
        (element as any).style.background = 'pink'
      });
    } else if (args.item.id === "customCut") {
      this.isCoping = false;
      this.treegrid.copy();
      this.treegrid.getSelectedRowIndexes().forEach((index, i) => {
        let element =
          document.querySelectorAll(".e-row")[index];
        (element as any).style.background = 'pink'
      });
    } else if (args.item.id === "customPasteNext") {
      let selectedRecords = this.treegrid.clipboardModule.copyContent.split('\n');
      let parentRecord = selectedRecords.splice(0, 1);
      selectedRecords.forEach((row, i) => {
        row = row.split('\t');
        let newRow = {
          taskID: ++this.taskID,
          taskName: row[1],
          startDate: row[2],
          endDate: row[3],
          progress: row[4],
          duration: row[5],
          priority: row[5],
          approved: row[6],
        }

        this.treegrid.addRecord(newRow, parentRecord.index + 1 + i, "Below");
        debugger;
        if (!this.isCoping) {
          selectedRecords.forEach((record) => {
            record = record.split('\t');
            this.treegrid.dataSource.splice(parseInt(record[0]), 1);
            this.treegrid.refresh();
          })
        }
      });

    } else if (args.item.id === "customPasteChild") {

      let selectedRecords = this.treegrid.clipboardModule.copyContent.split('\n');
      selectedRecords.forEach((row, i) => {
        row = row.split('\t');
        let newRow = {
          taskID: ++this.taskID,
          taskName: row[1],
          startDate: row[2],
          endDate: row[3],
          progress: row[4],
          duration: row[5],
          priority: row[5],
          approved: row[6],
        }

        this.treegrid.addRecord(newRow, this.rowIndex, "Child");
        if (!this.isCoping) {
          selectedRecords.forEach((record) => {
            record = record.split('\t');
            this.treegrid.deleteRecord(record[0]);
          })
        }
      });
    } else if (args.item.id === "customText") {
      this.column.type = "string";
      this.column.editType = "stringedit";
      this.treegrid.refreshColumns();
    } else if (args.item.id === "customNumber") {
      this.column.type = "number";
      this.column.editType = "numericedit";
      this.treegrid.refreshColumns();
    } else if (args.item.id === "customDate") {
    } else if (args.item.id === "customDropwDownList") {
    } else if (args.item.id === "customBoolean") {
    } else if (args.item.id === "customAddRow") {
      let data = {
        taskID: ++this.taskID,
        taskName: "",
        startDate: new Date(),
        endDate: new Date(),
        progress: 0,
        duration: 0,
        priority: "Normal",
        approved: false,
        subtasks: []
      };
      this.treegrid.addRecord(data, 0);
    } else if (args.item.id === "customAddChildRow") {
      debugger;
      let childRow = {
        taskID: ++this.taskID,
        taskName: "",
        startDate: new Date(),
        endDate: new Date(),
        progress: 0,
        duration: 0,
        priority: "Normal",
        approved: false,
      };

      this.treegrid.addRecord(childRow, this.rowIndex, "Child");
    }
  }
}
